
import './App.css';
import Header from './components/Header';
import { Link } from "react-router-dom";

import Overview from './components/Overview';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { useEffect, useState } from 'react';
import {
  BrowserRouter,
  Route,
  Routes,
  useNavigate,
} from "react-router-dom";
import TableDetail from './components/TableDetailCharts';
import Imagenet from './components/Imagenet';

function RedirectComponent({pathName}){
  const navigate = useNavigate();
  useEffect(() => {
    navigate(pathName, { replace: true });
  },[navigate])
  return ""
}

function App() {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const handleMLBenchClick = () => {
    window.open('https://www.github.com/collabora/mlbench', '_blank');
  };
  const navigate = useNavigate();

  return (
    <div className="App">
      <Header handleClose={handleClose} handleShow={handleShow} />
      <BrowserRouter basename="/MLBench">
        <Routes>
          <Route path="/" element={<RedirectComponent pathName={"/imagenet"}/>} />
          <Route path="/imagenet" element={<Imagenet />}/>
          <Route path="/imagenet/:id" element={<TableDetail />} />
          <Route path="/overview" element={<Overview />} />
        </Routes>
      </BrowserRouter>
      <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title class="clickable-logo" onClick={handleMLBenchClick}>MLBench</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <ul>
            <li>
              {/* <a
                className="menu-options d-flex align-items-center justify-content-start"
                href={"./overview"}
              >
                Overview
              </a> */}
              <div
                className="menu-options d-flex align-items-center justify-content-start"
                onClick={() => {
                  navigate("/overview");
                  handleClose();
                }}
              >
                Overview
              </div>
            </li>
            <li>
              {/* <a
                className="menu-options d-flex align-items-center justify-content-start"
                href={"./imagenet"}
              >
                ImageNet
              </a> */}
              <div
                className="menu-options d-flex align-items-center justify-content-start"
                onClick={() => {
                  navigate("/imagenet");
                  handleClose();
                }}
              >
                ImageNet
              </div>
            </li>
            {/* <li>Coco</li>
            <li>LibriSpeech</li> */}
          </ul>
        </Offcanvas.Body>
      </Offcanvas>
    </div>
  );
};

export default App;
